const data = {
	'console' : 'http://console.denha.loc',
	'ststic'  : 'http://console.denha.loc/ststic',
	'vendor'  : 'http://console.denha.loc/vendor',
	'logo' 	  : 'data:image/png;base64,',
	unserializeArray(obj) {
		let url = '',i=0;
		for(var key in obj){
			if(i == 0){
				url = url +  '?' + key + '=' + obj[key];
			}else{
				url = url +  '&' + key + '=' + obj[key];
			}
			i++;
		}
		return url;
  	},
};

export default {
  data
};


